// import store from "./dataBase";
const ADD_POST = 'ADD-POST';

// let startValue =  store.getDataBase();

const addPostReducer = (dataBase, action) => {
    switch (action.type) {
        case ADD_POST:
            let newPost = {
                userId: 5,
                name: 'Bed Cat',
                imgLogo: 'https://c.ndtvimg.com/2020-08/h5mk7js_cat-generic_625x300_28_August_20.jpg',
                text: action.text,
                post: action.text
            };
            dataBase.push(newPost);
            return dataBase;
        default:
            return dataBase;
    }
};
export const addPostAction = (text) => {
    return { type: ADD_POST, text: text }
};
export default addPostReducer;
