const SEND_MESSAGE = 'catSocial/message/SEND_MESSAGE';

let initState = {
    myMessages: ["Test message"],
}
const messageReducer = (state = initState, action) => {
    switch (action.type) {

        case SEND_MESSAGE: {
            return {
                ...state,
                myMessages: [...state.myMessages, action.myMessages]
            }
        }
        default:
            return state;
    }

};

export const sendMessageCreator = (myMessages) => ({ type: SEND_MESSAGE, myMessages });

export default messageReducer;





   