import React from 'react';
import Profile from './Profile'
import { connect } from "react-redux";
import { userProfile, getStatus, updateStatus, getFollowStatus, savePhoto } from '../../../redux/profileReducer';
import {
    useLocation,
    useNavigate,
    useParams,
} from "react-router-dom";
import { withAuthRedirect } from '../../../hoc/withAuthRedirect';
import { compose } from 'redux';
import { follow, toggleIsFollowProgress, unfollow } from '../../../redux/usersReducer';
import { getBestFriends } from '../../../redux/friendsReducer';

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }

    return ComponentWithRouterProp;
}

class ProfileContainer extends React.Component {
    
    componentDidMount() {
        let userId = this.props.router.params.userId;
        if (!userId) {
            userId = this.props.myId;
        };
        this.props.userProfile(userId);
        this.props.getStatus(userId);
        this.props.getFollowStatus(userId);
    }
    componentDidUpdate(prevProps) {
        
        let userId = this.props.router.params.userId;
         if (!userId) {
            userId = this.props.myId;
        };
    if (prevProps.router.params.userId !== this.props.router.params.userId) {
        this.props.userProfile(userId);
        this.props.getStatus(userId);
        this.props.getFollowStatus(userId);
        this.props.getBestFriends();
    }
}

    render() {
        return (
            <Profile isOwner={!this.props.router.params.userId}
                profile={this.props.profile}
                status={this.props.status}
                updateStatus={this.props.updateStatus}
                followStatus={this.props.followStatus}
                unfollow={this.props.unfollow}
                follow={this.props.follow}
                followInProgres={this.props.followInProgres}
                savePhoto={this.props.savePhoto}/>
        )
    }
};

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    followStatus: state.profilePage.followStatus,
    myId: state.auth.id,
    isAuth: state.auth.isAuth,
    users: state.usersPage.users,
    followInProgres: state.usersPage.followInProgres,
});

export default compose(
    connect(mapStateToProps, {
        userProfile,
        getStatus,
        getFollowStatus,
        getBestFriends,
        updateStatus,
        follow,
        unfollow,
        toggleIsFollowProgress,
        savePhoto
    }),
    withRouter,
    withAuthRedirect)(ProfileContainer);