import React from 'react';
import {Formik, Form, Field, ErrorMessage} from "formik";
import * as Yup from "yup";
import s from './Messages.module.css';
import { sendMessageCreator } from '../../redux/messageReducer';
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';

const validationSchemaLoginForm = Yup.object().shape( {

   myMessages: Yup.string()
      .min( 2, "Must be longer than 2 characters" )
      .max( 100, "Must be shorter than 100 characters" )
      .required( "Required 2" )
} );


const NewMessageFormik = (props) => {
if (!props.isAuth) {
        return <Navigate to={"/"}/>
   }
   return (
      <div>
         <h2> Send message </h2>
         <Formik
            initialValues={{
               myMessages: "",
            }}
            validationSchema={validationSchemaLoginForm}
            onSubmit={(values, {resetForm}) => {
               props.sendMessageCreator(values.myMessages);
               resetForm({ values: "" });
            }}
         >
            {() => (
               <Form>
                  <div>
                     <Field
                        name={'myMessages'}
                        type={'text'}
                        placeholder={'Enter your message'} />
                  </div>
                  <ErrorMessage name="myMessages" component="div" className={s.error}/>
                  <button type={'submit'}>Send message</button> 
               </Form>   
            )}
         </Formik>
      </div>
   )
}

const mapStateToProps = (state) => ({
   isAuth: state.auth.isAuth,
})
export default connect(mapStateToProps, { sendMessageCreator })(NewMessageFormik);

