import React from "react";
import { connect } from "react-redux";
import { follow, unfollow, setCurrentPage, toggleIsFollowProgress, getUsers } from '../../redux/usersReducer';
import Users from './Users';
import Preloader from "../common/preloader/preloader";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getBestFriends } from "../../redux/friendsReducer";
import {
    selectCurrentPage, selectfollowInProgres, selectGetUsers, selectIsFetching, selectPageSize, selectTotalUsersCount} from "../../redux/selectorUsers";

class UsersContainer extends React.Component {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize);
    }

    onPageChanged = (currentPage) => {
        this.props.getUsers(currentPage, this.props.pageSize);
    }
    componentDidUpdate(){
    this.props.getBestFriends();
    }

    render() {
        return <>
            {this.props.isFetching ? <Preloader /> : null}
            <Users totalItemsCount={this.props.totalUsersCount}
            pageSize={this.props.pageSize}
            currentPage={this.props.currentPage}
            users={this.props.users}
            onPageChanged={this.onPageChanged}
            unfollow={this.props.unfollow}
            follow={this.props.follow}
            followInProgres={this.props.followInProgres} /></>
        }  
}

let mapStateToProps = (state) => {
    return {
        users: selectGetUsers(state),
        pageSize: selectPageSize(state),
        totalUsersCount: selectTotalUsersCount(state),
        currentPage: selectCurrentPage(state),
        isFetching: selectIsFetching(state),
        followInProgres: selectfollowInProgres(state),
    }
}

export default compose(
    connect(mapStateToProps, {
        follow,
        unfollow,
        setCurrentPage,
        toggleIsFollowProgress,
        getBestFriends,
        getUsers}),
    withAuthRedirect)(UsersContainer);