import React from "react";
import Paginator from "../common/Paginator/Paginator";
import User from "./User";

let Users = ({currentPage, totalItemsCount, pageSize, onPageChanged, ...props }) => {
 
    return (
        <div>
            <Paginator currentPage={currentPage} onPageChanged={onPageChanged} totalItemsCount={totalItemsCount} pageSize={pageSize}/>
            {props.users.map(u => <User user={u}
                followInProgres={props.followInProgres}
                key={u.id}
                unfollow={props.unfollow}
                follow={props.follow}/>
            )}
        </div>
    );
}


export default Users;