import { applyMiddleware, combineReducers, compose, createStore } from 'redux';
import usersReducer from './usersReducer';
import messageReducer from './messageReducer';
import profileReducer from './profileReducer';
import authReducer from './authReducer';
import thunkMiddleware from 'redux-thunk';
import { reducer as formReducer } from 'redux-form';
import appReducer from './appReducer';
import friendsReducer from './friendsReducer';


let reducers = combineReducers({
    messagePage: messageReducer,
    profilePage: profileReducer,
    usersPage: usersReducer,
    friendsPage:friendsReducer,
    auth: authReducer,
    form: formReducer,
    app: appReducer
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const dataBaseRedux = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)
  ));

// let dataBaseRedux = createStore(reducers, applyMiddleware(thunkMiddleware));

window._store_ = dataBaseRedux;

export default dataBaseRedux;