import store from './redux/dataBase';
import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import './index.css';
import App from './App';
import { Provider } from 'react-redux';
import dataBaseRedux from './redux/reduxDataBase';



let rerenderPage = (dataBase) => {    
    ReactDOM.render(

    <React.StrictMode>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <Provider store={dataBaseRedux}>
                <App dataBase={dataBase} dispatch={store.dispatch.bind(store)} store={store}/>
            </Provider>
        </BrowserRouter>
    </React.StrictMode>,
    document.getElementById('root')
    );
}
rerenderPage(store.getDataBase());

store.subscribe(rerenderPage);

