import React, { Component } from 'react';
import './App.css';
import Nav from './components/Nav/Nav';
import Content from './components/Content/Content';
import UsersContainer from './components/Users/UsersContainer';
import Music from './components/Music/Music';
import Settings from './components/Settings/Settings';
import {Routes, Route} from "react-router-dom";
import RightPanel from './components/RightPanel/RightPanel';
import MessageContainer from './components/Messages/MessageContainer';
import ProfileContainer from './components/Content/Profile/ProfileContainer'
import HeaderContainer from './components/Header/HeaderContainer';
import Login from './components/Login/Login';
import LoginNew from './components/Login/LoginNew';
import { connect } from 'react-redux';
import { initApp } from './redux/appReducer';
import Preloader from './components/common/preloader/preloader';


class App extends Component {
      componentDidMount() {
        this.props.initApp();
    }
  render() {
    if (!this.props.init) {
      return <Preloader />
    }
    return (
      <div className='app-wrapper'>
        <HeaderContainer />
        <Nav dataBase={this.props.dataBase} />
        <div className='container'>
          <Routes>
            <Route path='/' element={<Content dataBase={this.props.dataBase} dispatch={this.props.dispatch} />} />
            <Route path='/profile/:userId' element={<ProfileContainer />} />
            <Route path='/profile/' element={<ProfileContainer />} />
            <Route path='/messages/*' element={<MessageContainer/>} />
            <Route path='/users' element={<UsersContainer />} />
            <Route path='/music' element={<Music />} />
            <Route path='/settings' element={<Settings />} />
            <Route path='/login' element={<Login />} />
            <Route path='/login-new' element={<LoginNew />} />
          </Routes>
        </div>
        <RightPanel dataBase={this.props.dataBase} />
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  init: state.app.init
})

export default connect(mapStateToProps, {initApp})(App);

