import React from "react";
import { connect } from "react-redux";
import s from './Messages.module.css';
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import Message from "./Message";
import { compose } from "redux";
import NewMessageFormik from "./NewMessageFormik";

class MessageContainer extends React.Component {

    render() {
        return (
            <div className={s.dialogs}>
                <div className={s.massages}>
                    <div><Message myMessages={this.props.myMessages}/></div>
                    <div><NewMessageFormik /> </div>   
                    </div>
            </div>
        )}  
}

let mapStateToProps = (state) => {
    return {
        myMessages: state.messagePage.myMessages,
    }
}

export default compose(
    connect(mapStateToProps), withAuthRedirect)(MessageContainer);