import React from 'react';
import {Formik, Form, Field, ErrorMessage} from "formik";
import * as Yup from "yup";
import s from './Login.module.css';
import { login } from '../../redux/authReducer';
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';


const validateLoginForm = values => {
   const errors = {};
   if (!values.email) {
      errors.email = 'Required 1';
   } else if (
      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test( values.email )
   ) {
      errors.email = 'Invalid email address';
   }
   return errors;
};

const validationSchemaLoginForm = Yup.object().shape( {

   password: Yup.string()
      .min( 2, "Must be longer than 2 characters" )
      .max( 10, "Must be shorter than 5 characters" )
      .required( "Required 2" )
} );


const LoginNew = (props) => {
if (props.isAuth) {
        return <Navigate to={"/"}/>
   }
   return (
      <div>
         <h2> Please Login New </h2>

         <Formik
            initialValues={{
               email: "",
               password: "",
               rememberMe: false
            }}
            validate={validateLoginForm}
            validationSchema={validationSchemaLoginForm}
            onSubmit={(values) => {
               console.log(values)
               props.login(values.email, values.password, values.rememberMe, values.captcha)
            }}
         >
            {() => (
               <Form>
                  <div>
                     <Field
                        name={'email'}
                        type={'text'}
                        placeholder={'e-mail'} />
                  </div>
                  <ErrorMessage name="email" component="div" className={s.error}/>

                  <div>
                     <Field
                        name={'password'}
                        type={'password'}
                        placeholder={'password'} />
                  </div>
                  <ErrorMessage name="password" component="div" className={s.error}/>

                  <div>
                     <Field
                        type={'checkbox'}
                        name={'rememberMe'}
                        id='rememberMe' />
                     <label htmlFor={'rememberMe'}> remember me </label>
                  </div>
                <div>{props.captchaURL && <div><div><img src={props.captchaURL} alt="captcha"/></div>
                <div>
                     <Field
                        name={'captcha'}
                        type={'captcha'}
                        placeholder={'captcha'} /></div></div>}
                </div>
                  <button type={'submit'}>Login</button>
                   { props.error && <div className={s.authError}> {props.error} </div>}
               </Form>
               
            )}
         </Formik>
      </div>
   )
}

const mapStateToProps = (state) => ({
   isAuth: state.auth.isAuth,
   captchaURL:state.auth.captchaURL,
})
export default connect (mapStateToProps, {login}) (LoginNew);
