import React from "react";
import { Field, reduxForm } from "redux-form";
import s from './NewPost.module.css'
import { maxLengthCreator, required } from "../../../utils/validators/validators";
import { Textarea } from "../../common/FormsControls/FornControls";

const maxLenght10= maxLengthCreator(10)

const NewPostForm = (props) => {
    return (
            <form onSubmit={props.handleSubmit}>
                <div>
                <Field placeholder={"Post"} name={"post"} component={Textarea} validate={[required, maxLenght10]}/> 
                </div>
                <div>
                    <button>Submit new post</button>
                </div>
            </form>
    )
}

const FormReduxForm = reduxForm({
    form:'post'
}) (NewPostForm)

const NewPost = (props) => {
    const onSubmit = (formData) => {
        console.log(formData)
    }
    return (
        <div className={s.loginPage}>
            <h3>Enter your new post</h3>
            <FormReduxForm onSubmit={onSubmit}/>
        </div>
    );
}
export default NewPost;