import React from "react";
import s from './Profile.module.css';
import ProfileStatusHooks from './ProfileStatusHooks';
import Contacts from './Contacts';
import catWork from '../../../assets/images/cat-work.jpg';
import catLookingJob from '../../../assets/images/cat-looking-job.jpg'

const ProfileData = (props) => {
    console.log(props);
return (
    <div>
        {props.props.isOwner && <div><button onClick={props.goToEditMode}>Edit Profile</button></div>}
        <div className={s.userName}><b>Name: </b>{props.props.profile.fullName}</div>
        <div className={s.userId}><b>User ID: </b>{props.props.profile.userId}</div>
        <div className={s.about}><b>Abuot Me: </b>{props.props.profile.aboutMe}</div>
        <ProfileStatusHooks status={props.props.status} updateStatus={props.props.updateStatus} />
        <div>
            {props.props.followStatus
                ? <button disabled={props.props.followInProgres.some(id => id === props.props.profile.userId)}
                    onClick={() => { props.props.unfollow(props.props.profile.userId) }}>Unfollow</button>
                : <button disabled={props.props.followInProgres.some(id => id === props.props.profile.userId)}
                    onClick={() => { props.props.follow(props.props.profile.userId) }}>Follow</button>}
        </div>
        <div>
            <div>
                <b>Loking for a job: </b><div>{props.props.profile.lokingForAJob
                    ? <img src={catWork} alt="cat worked" className={s.catWork} />
                    : <img src={catLookingJob} alt="cat looking for a job" className={s.catWork} />}</div>
            </div>
            <div>
                <b>My proffesional skills: </b>{props.props.profile.lookingForAJobDescription}
            </div>
        </div>
        <div><b>Contacts:</b></div>
        <div>{Object.keys(props.props.profile.contacts).map(key => {
            return <Contacts contactTitle={key} contactValue={props.props.profile.contacts[key]} key={key}/>
        })}</div>
        {/* <ul className={s.contacts}>
            <li><a href={`https://${props.profile.contacts.facebook}`}>Facebook</a></li>
            <li><a href={props.profile.contacts.website}>Web site</a></li>
            <li><a href={props.profile.contacts.twitter}>twitter</a></li>
            <li><a href={`https://${props.profile.contacts.instagram}`}>Instagram</a></li>
        </ul> */}
    </div>);
}

export default ProfileData