import React from 'react';
import s from './Header.module.css';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/1.jpg';
import noLogo from '../../assets/images/nologo.jpg';

const Header = (props) => {
    return (
        <header className={s.header}>
        <div>
                <Link to="/"><img src={logo} className={s.logo} alt="Logo" />
        </Link></div>
            <div><h1>First CatSocial network</h1></div>
            <span className={s.loginBlock}>
                <span className={s.login}>
                    {props.isAuth
                        ? <div>{props.login} <button onClick={props.logout}>LogOut</button></div>
                        : <Link to={'/login'}>Login</Link>}</span>
                    <span>{props.isAuth
                    ? <Link to={`/profile/` + props.id} className={`${s.navLink} ${s.linkHover}`}>
                        <img src={props.profile != null ? props.profile.photos.small : noLogo} className={s.mylogo} alt="User Logo" /></Link>
                    :<Link to={`/login-new/`} className={`${s.navLink} ${s.linkHover}`}>
                        <img src={noLogo} className={s.mylogo} alt="No Logo" /></Link>
                    }</span>
                    
            </span>
    </header>)
};

export default Header