import { usersAPI, followAPI, unfollowAPI } from "../api/api";
import { updateObjectInArrey } from "../utils/objectHelpers";

const FOLLOW = 'catSocial/users/FOLLOW';
const UNFOLLOW = 'catSocial/users/UNFOLLOW';
const SET_USERS = 'catSocial/users/SET_USERS';
const SET_CURRENT_PAGE = 'catSocial/users/SET_CURRENT_PAGE';
const SET_TOTAL_USER_COUNT = 'catSocial/users/SET_TOTAL_USER_COUNT';
const TOGGLE_IS_FETCHING = 'catSocial/users/TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOW_PROGRESS = 'catSocial/users/TOGGLE_IS_FOLLOW_PROGRESS';

let initState = {   
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followInProgres: []
}
const usersReducer = (state = initState, action) => {
    switch (action.type) {    
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArrey(state.users, action.userId, "id", { followed: false })
            }
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArrey(state.users, action.userId, "id", { followed: true })
            }
        case SET_USERS: {
            return { ...state, users: action.users}
        }
            case SET_CURRENT_PAGE: {
            return { ...state, currentPage: action.currentPage }
        }
            case SET_TOTAL_USER_COUNT: {
            return { ...state, totalUsersCount: action.count }
        }
            case TOGGLE_IS_FETCHING: {
            return { ...state, isFetching: action.isFetching }
        }
            case TOGGLE_IS_FOLLOW_PROGRESS: {
            return {
                ...state, followInProgres: action.isFetching
                    ? [...state.followInProgres, action.userId]
                    : state.followInProgres.filter(id => id !== action.userId)
            }
        }
        default:
            return state;
    }   
};

export const followSucces = (userId) => ({ type: FOLLOW, userId });
export const unfollowSucces = (userId) => ({ type: UNFOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users });
export const setCurrentPage = (currentPage) => ({ type: SET_CURRENT_PAGE, currentPage });
export const setTotalUsersCount = (totalUsersCount) => ({ type: SET_TOTAL_USER_COUNT, count: totalUsersCount });
export const toggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching });
export const toggleIsFollowProgress = (isFetching, userId) => ({ type: TOGGLE_IS_FOLLOW_PROGRESS, isFetching, userId });

export const getUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(toggleIsFetching(true));
        const data = await usersAPI.getUsers(currentPage, pageSize);
            dispatch(toggleIsFetching(false));
            dispatch(setUsers(data.items));
            dispatch(setCurrentPage(currentPage));
            dispatch(setTotalUsersCount(data.totalCount));
        };
}
const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch (toggleIsFollowProgress(true, userId));
        const response = await apiMethod(userId);
        if (response.data.resultCode === 0) {
            dispatch (actionCreator(userId))
        }
        dispatch (toggleIsFollowProgress(false, userId));
        };

export const follow = (userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId, followAPI.getFollow, followSucces);
        };
}
    
export const unfollow = (userId) => {
    return async (dispatch) => {
        followUnfollowFlow(dispatch, userId, unfollowAPI.getUnFollow, unfollowSucces);
        };
}

export default usersReducer;