import React from "react";
import { Formik, Form, Field } from "formik";
import s from './Profile.module.css';
import ProfileStatusHooks from './ProfileStatusHooks';
import Contacts from './Contacts';
import catWork from '../../../assets/images/cat-work.jpg';
import catLookingJob from '../../../assets/images/cat-looking-job.jpg'

const ProfileDataForm = (props) => {
    
    return (<div>
        <ProfileStatusHooks status={props.props.status} updateStatus={props.props.updateStatus} />
         <Formik
            initialValues={{
               abuotMe: "",
               lokingForAJob: false,
               mySkills: "",
                facebook: "",
                website: "",
                twitter: "",
                instagram: "",
                youtube: "",
                github: "",
               mainLink: "",
            }}
            // validate={validateLoginForm}
            // validationSchema={validationSchemaLoginForm}
            onSubmit={(values) => {
               console.log(values)
                props.login(values.abuotMe,
                    values.lokingForAJob,
                    values.mySkills,
                    values.facebook,
                    values.website,
                    values.twitter,
                    values.instagram,
                    values.youtube,
                    values.github,
                    values.mainLink,)
            }}>
            {() => (
                <Form>
                    {<div><button onClick={props.goToEditMode}>Save</button></div>}
                    <div className={s.userName}><b>Name: </b>{props.props.profile.fullName}</div>
                    <div className={s.userId}><b>User ID: </b>{props.props.profile.userId}</div>
                    <div className={s.about}><b>Abuot Me: </b><Field
                        name={'abuotMe'}
                        type={'text'}
                        placeholder={'abuotMe'} />{props.props.profile.aboutMe}</div>
        
                    <div>
                        {props.props.followStatus
                            ? <button disabled={props.props.followInProgres.some(id => id === props.props.profile.userId)}
                                onClick={() => { props.props.unfollow(props.props.profile.userId) }}>Unfollow</button>
                            : <button disabled={props.props.followInProgres.some(id => id === props.props.profile.userId)}
                                onClick={() => { props.props.follow(props.props.profile.userId) }}>Follow</button>}
                    </div>
                    <div>
                        <div>
                            <b>Loking for a job: </b><Field
                        name={'lokingForAJob'}
                        type={'text'}
                        placeholder={'lokingForAJob'} /><div>{props.props.profile.lokingForAJob
                                ? <img src={catWork} alt="cat worked" className={s.catWork} />
                                : <img src={catLookingJob} alt="cat looking for a job" className={s.catWork} />}</div>
                        </div>
                        <div>
                            <b>My proffesional skills: </b><Field
                        name={'mySkills'}
                        type={'text'}
                        placeholder={'mySkills'} />{props.props.profile.lookingForAJobDescription}
                        </div>
                    </div>
                    <div><b>Contacts:</b></div>
                    <div>{Object.keys(props.props.profile.contacts).map(key => {
                        return <ContactsForm contactTitle={key} contactValue={props.props.profile.contacts[key]} key={key} />
                    })}</div>
                    {/* <ul className={s.contacts}>
            <li><a href={`https://${props.profile.contacts.facebook}`}>Facebook</a></li>
            <li><a href={props.profile.contacts.website}>Web site</a></li>
            <li><a href={props.profile.contacts.twitter}>twitter</a></li>
            <li><a href={`https://${props.profile.contacts.instagram}`}>Instagram</a></li>
        </ul> */}
                </Form>)}
    </Formik></div>
    )
}

const ContactsForm = ({contactTitle, contactValue}) => {
    return <div><b>{contactTitle}: </b><Field
                        name={contactTitle}
                        type={'text'}
                        placeholder={contactTitle} />{contactValue}</div>
}

export default ProfileDataForm;