import { stopSubmit } from 'redux-form';
import { myAPI, logIn, logOut, securityAPI } from '../api/api';


const SET_USER_DATA = 'catSocial/auth/SET_USER_DATA';
const SET_CAPTCHA_URL = 'catSocial/auth/SET_CAPTCHA_URL';

let initState = {
    
    id: null,
    email: null,
    login: null,
    isAuth: false,
    captchaURL: null,

}
const authReducer = (state = initState, action) => {
    switch (action.type) {
        case SET_CAPTCHA_URL:
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        default:
        return state;
    }
    
};

export const setAuthUserData = (id, email, login, isAuth) => ({ type: SET_USER_DATA, payload: { id, email, login, isAuth } });
export const setCaptchaUrl = (captchaURL) => ({ type: SET_CAPTCHA_URL, payload: { captchaURL } });

export const authMe =() => async (dispatch) => {
    let data = await myAPI.getMy();
    if (data.resultCode === 0) {
        let { id, email, login} = data.data;
        dispatch (setAuthUserData(id, email, login, true));
        }
    };
    

export const login =(email, password, rememberMe, captcha) => 
    async(dispatch) => {
        const data = await logIn.getLogin(email, password, rememberMe, captcha);
        if (data.resultCode === 0) {
            dispatch(authMe())
        }
    else {
        if (data.resultCode === 10) {
            dispatch(getCaptcha());
            }
            // let messageError= data.messages.lenght > 0 ? data.messages[0]:"Some error"
        dispatch(stopSubmit('login', {_error: data.messages}));
        } };
    

export const getCaptcha =() => async (dispatch) => {
    const response = await securityAPI.getCaptcha();
    const captchaURL = response.data.url;
        dispatch(setCaptchaUrl(captchaURL));
};

export const logout =() => 
     async(dispatch) => {
         const data = await logOut.getLogOut();
    if (data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))
        } };
    


export default authReducer;
