import { friendsAPI} from "../api/api";


const SET_BEST_FRIENDS = 'catSocial/friends/SET_BEST_FRIENDS';

let initState = {   
    bestFriends: [],
}
const friendsReducer = (state = initState, action) => {
    switch (action.type) {    
        case SET_BEST_FRIENDS: {
            return { ...state, bestFriends: action.bestFriends } 
        }
        default:
            return state;
    }
    
};

export const setBestFriends = (bestFriends) => ({ type: SET_BEST_FRIENDS, bestFriends });

export const getBestFriends = () => {
    return async (dispatch) => {
        const data = await friendsAPI.getFriends()
            dispatch(setBestFriends(data.items));
        }
    }

export default friendsReducer;
   