import React from "react";
import preloader from '../../../assets/images/preloader.gif';


let Preloader = (props) => {
    return <div>
       <img src={preloader} alt="preloader" style={{
            width: '400px',
            display: 'block',
            marginLeft: 'auto',
            marginRight: 'auto'
        }}/>
        </div>
}
export default Preloader;