import React, {useEffect, useState} from 'react';
import s from './Profile.module.css';
import Preloader from '../../common/preloader/preloader';
import userPhoto from '../../../assets/images/1.jpg';
import ProfileData from './ProfileData';
import ProfileDataForm from './ProfileDataForm';

const Profile = (props) => {

    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status)

    if (!props.profile) {
        return <Preloader />;
    }
    const onLogoPhotoSelected = (e) => {

        if (e.target.files.length) {
            console.log(e.target.files[0])
            props.savePhoto(e.target.files[0]);
        }
    }
    return <div className={s.content}>
    <div><img src={props.profile.photos.small != null ? props.profile.photos.large : userPhoto} alt="User Large" /></div>
        <div>{props.isOwner && <input type={"file"} onChange={onLogoPhotoSelected} />}</div>
        {editMode
            ? <ProfileDataForm props={props}/>
            : <ProfileData props={props} goToEditMode={()=>{setEditMode(true)}} />
        }
</div>
};

export default Profile;