import { createSelector } from 'reselect';


const GetUsersForSelect = (state) => {
    return state.usersPage.users;
}


export const selectGetUsers = createSelector(GetUsersForSelect, (users) => {
    // console.log(users);
    return users.filter(u => true)
})
