import React from "react";
import s from './Friends.module.css'
import { Link} from "react-router-dom";
import userPhoto from '../../assets/images/1.jpg'

const Friends = React.memo (props => {
    let friendsElements = props.bestFriends.map((el) => {
        return (<div key={el.id}>
            <Link to={`/profile/`+ el.id} className={`${s.navLink} ${s.linkHover}`}>
                <div className={s.friendsBar}>
                    <img src={el.photos.small != null ? el.photos.small : userPhoto} className={s.LogoImage} alt={el.name} />
                    <div>{el.name}</div>
                </div>
            </Link>
            </div>
        )
    })
    return (
        <div className={s.dialog}>
            <h3>My Best Friends</h3>
                {friendsElements}
            </div>
        )
});
export default Friends;