import React from "react";
import s from './Users.module.css';
import { Link } from "react-router-dom";
import userPhoto from '../../assets/images/1.jpg'


let User = ({user, followInProgres, unfollow, follow }) => {
    return (
        <div key={user.id} className={s.userslist}>
                <span className={s.userInfo}>
                    <Link to={`/profile/`+user.id} className={`${s.navLink} ${s.linkHover}`}>
                        <div><img src={user.photos.small != null ? user.photos.small : userPhoto} className={s.LogoImage} alt="User Logo" />
                        </div>
                    </Link>
                    <div>
                        {user.followed   
                            ? <button disabled={followInProgres.some(id => id === user.id)}
                                onClick={() => {unfollow(user.id)}}>Unfollow</button>
                            : <button disabled={followInProgres.some(id => id === user.id)}
                                onClick={() => {follow(user.id)}}>Follow</button>}
                    </div>
                </span>
                <span className={s.userMessage}>
                    <span>
                        <div>{user.name}</div>
                        <div>{user.status}</div>
                    </span>
                </span>
            </div>
            )}


export default User;