import React from "react";
import { Field, reduxForm } from "redux-form";
import { maxLengthCreator, required } from "../../utils/validators/validators";
import { Input } from "../common/FormsControls/FornControls";
import { connect } from 'react-redux';
import { login } from '../../redux/authReducer';
import { Navigate } from 'react-router-dom';
import s from '../../components/common/FormsControls/FormsControls.module.css'

const maxLenght25 = maxLengthCreator(25);

const LoginForm = ({handleSubmit, error, captchaURL}) => {
    return (
            <form onSubmit={handleSubmit}>
                <div>
                <Field placeholder={"Email"} name={"email"} component={Input} validate={[required, maxLenght25]}/> 
                </div>
                <div>
                    <Field placeholder={"Password"}  name={"password"} type={"password"} component={Input} validate={[required, maxLenght25]}/>
                </div>
                <div>
                    <Field component={Input} name={"rememberMe"} type={"checkbox"} validate={[required]}/>Remember Me
            </div>
            <div>{captchaURL && <div><img src={captchaURL} alt="captcha"/> 
                <Field placeholder={"captcha"} name={"captcha"} component={Input} validate={[required]}/></div>}
            </div>
                <div>
                    <button>Login</button>
            </div>
             { error && <div className={s.authError}> {error} </div>}
            </form>
    )
}

const LoginReduxForm = reduxForm({
    form:'login'
}) (LoginForm)

const Login = (props) => {
    const onSubmit = (formData) => {
        props.login(formData.email, formData.password, formData.rememberMe, formData.captcha)
    }
    if (props.isAuth) {
        return <Navigate to={"/"}/>
    }
    return (
        <div className={s.loginPage}>
            <h1>Please Login</h1>
            <LoginReduxForm onSubmit={onSubmit} captchaURL={props.captchaURL}/>
        </div>
    );
}
const mapStateToProps = (state) => ({
    captchaURL:state.auth.captchaURL,
    isAuth:state.auth.isAuth
})
export default connect (mapStateToProps, {login}) (Login);