import React from 'react';
import s from './Nav.module.css';
import { Link } from "react-router-dom";

const Nav = () => {
    return (
        <nav className={s.nav}> 
        <div><Link to='/' className={`${s.navLink} ${s.linkHover}`}>Home</Link></div>
         <div><Link to='/profile' className={`${s.navLink} ${s.linkHover}`}>Profile</Link></div>
        <div><Link to="/messages" className={`${s.navLink} ${s.linkHover}`}>Messages</Link></div>
         <div><Link to="/users" className={`${s.navLink} ${s.linkHover}`}>Users</Link></div>
        <div><Link to="/music" className={`${s.navLink} ${s.linkHover}`}>Music</Link></div>
        <div><Link to="/settings" className={`${s.navLink} ${s.linkHover}`}>Settings</Link></div>
        <div><Link to="/login" className={`${s.navLink} ${s.linkHover}`}>Login</Link></div>
        <div><Link to="/login-new" className={`${s.navLink} ${s.linkHover}`}>Login New</Link></div>
    </nav>)
};

export default Nav;